.error {
  display: flex;
  flex-direction: column;
  text-align: center;
  color: #ff6060;

  h1 {
    font-weight: 700;
    font-size: 280px;
    margin: 140px 0px 140px;
  }

  .error_text {
    font-size: 45px;
    margin-bottom: 140px;
    font-weight: 500;
  }

  .error_link {
    color: #ff6060;
    font-size: 23px;
    margin-bottom: 140px;
  }

  @media (max-width: 1024px) {
    h1 {
      font-size: 150px;
      margin: 113px 0;
    }

    .error_text {
      font-size: 24px;
      margin: 0 60px 113px;
    }

    .error_link {
      font-size: 16px;
      margin-bottom: 113px;
    }
  }

  @media (max-width: 767.98px) {
    h1 {
      font-size: 90px;
    }

    .error_text {
      font-size: 18px;
    }

    .error_link {
      font-size: 14px;
    }
  }
}